.App {
    text-align: center;
  }
  
  .array-container {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin: 20px 0;
  }
  
  .controls {
    margin-top: 20px;
  }
  
  button {
    margin: 10px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
  }
  
  button:disabled {
    background-color: grey;
    /* cursor: not-allowed; */
  }